import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-catalogcourse',
  templateUrl: './catalogcourse.component.html',
  styleUrls: ['./catalogcourse.component.css']
})
export class CatalogcourseComponent implements OnInit {
  public search = '';
  public searchList = [
    {
      id: 1, 
      name : 'Desain Grafis'
    },
    {
      id: 2, 
      name : 'Data Science'
    },
    {
      id: 3, 
      name : 'Engineering'
    }, 
    {
      id: 4, 
      name : 'IT'
    },
    {
      id: 5, 
      name : 'Marketing'
    } 
  ];
  public searchListPopular = [ 
    {
      id: 1, 
      name : 'IT'
    },
    {
      id: 2, 
      name : 'Engineering'
    },
    {
      id: 3, 
      name : 'Marketing'
    },
    {
      id: 4, 
      name : 'Data Science'
    },
    {
      id: 5, 
      name : 'Desain Grafis'
    }
  ];
 

  modalStatus: boolean;
  overlay: boolean;
  constructor() { }

  ngOnInit() {
    this.modalStatus = false
    this.overlay = false
  }
  removeSearchTag (key) {
      this.searchList.splice(key, 1);
  }
  addSearchTag (name) {
    this.search = name;
  }
  openModal () {
    this.modalStatus = true
  }
  closeModal () {
    this.modalStatus = false
    this.overlay = false
    this.status = false;  
  }
  status: boolean = false;
  openCategory () {
    this.status = true;  
    this.overlay = true
  }
  closeCatModal () {
    this.overlay = false
    this.status = false;  
  }
}
