import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-lmsassignment',
  templateUrl: './lmsassignment.component.html',
  styleUrls: ['./lmsassignment.component.css','../../assets/css/progressfile.css']
})
export class LmsassignmentComponent implements OnInit {
 
  constructor() { }
  detilkursus:Boolean
  sesikursus:Boolean

  ngOnInit() {
    this.modalStatus = true
    this.sesikursus=true;
  }
  DetilKursus(){
    this.sesikursus=false;
    this.detilkursus=true;
  }
  SesiKursus(){
    this.sesikursus=true;
    this.detilkursus=false;
  }
  modalStatus: boolean;
 
  openModal () {
    this.modalStatus = true
  }
  closeModal () {
    this.modalStatus = false
  }

}
