import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-lmsuploadassignment',
  templateUrl: './lmsuploadassignment.component.html',
  styleUrls: ['./lmsuploadassignment.component.css']
})
export class LmsuploadassignmentComponent implements OnInit {
  showModal: boolean;
  content: string;
  title: string;
  //Bootstrap Modal Open event
  show()
  {
    this.showModal = true; // Show-Hide Modal Check
    this.content = "This is content!!"; // Dynamic Data
    this.title = "This is title!!";    // Dynamic Data
  }
  //Bootstrap Modal Close event
  hide()
  {
    this.showModal = false;
  }
  
  constructor() { }
  modalStatus: boolean;
  ngOnInit() {
    this.modalStatus = false
  }
  openModal () {
    this.modalStatus = true
  }
  closeModal () {
    this.modalStatus = false
  }
}
