import { AppComponent } from './app.component';
import { ModuleWithProviders } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { LoginComponent } from './login/login.component';
import { HomepageComponent } from './homepage/homepage.component';
import { ForgotpasswordComponent } from './forgotpassword/forgotpassword.component';
import { ForgotpasswordsuccessComponent } from './forgotpasswordsuccess/forgotpasswordsuccess.component';
import { FounderpageComponent } from './founderpage/founderpage.component';
import { AboutuspageComponent } from './aboutuspage/aboutuspage.component';
import { VacanciesComponent } from './vacancies/vacancies.component';
import { CatalogcourseComponent } from './catalogcourse/catalogcourse.component';
import { SearchresultComponent } from './searchresult/searchresult.component';
import { SeeallComponent } from './seeall/seeall.component';
import { VacancydetailsComponent } from './vacancydetails/vacancydetails.component';
import { CoursedetailsComponent } from './coursedetails/coursedetails.component';
import { LMSpageComponent } from './lmspage/lmspage.component';
import { LmsquizComponent } from './lmsquiz/lmsquiz.component';
import { LmspdfComponent } from './lmspdf/lmspdf.component';
import { LmsquizresultsComponent } from './lmsquizresults/lmsquizresults.component';
import { LmsdiscussionComponent } from './lmsdiscussion/lmsdiscussion.component';
import { LmsuploadassignmentComponent } from './lmsuploadassignment/lmsuploadassignment.component';
import { LmsquizpageComponent } from './lmsquizpage/lmsquizpage.component';
import { LmsdiscussionpageComponent } from './lmsdiscussionpage/lmsdiscussionpage.component';
import { LmsassignmentComponent } from './lmsassignment/lmsassignment.component';
import { ProfileComponent } from './profile/profile.component';
import { DashbordbadgesComponent } from './dashbordbadges/dashbordbadges.component';
import { DashbordemptyComponent } from './dashbordempty/dashbordempty.component';
import { MyclassComponent } from './myclass/myclass.component';


export const routes: Routes = [
    { path: '', component: LoginComponent },    
    { path: 'login', component: LoginComponent },
    { path: 'home', component: HomepageComponent },
    { path: 'forgotpassword', component: ForgotpasswordComponent },
    { path: 'forgotpasswordsuccess', component: ForgotpasswordsuccessComponent },
    { path: 'founderpage', component: FounderpageComponent },
    { path: 'aboutUs', component: AboutuspageComponent},
    { path: 'lowongan', component: VacanciesComponent},
    { path: 'catalogcourse', component: CatalogcourseComponent},
    { path: 'searchresult', component: SearchresultComponent},
    { path: 'seeall', component: SeeallComponent},
    { path: 'vacancydetails', component: VacancydetailsComponent},
    { path: 'coursedetails', component: CoursedetailsComponent},
    { path: 'lmspage', component: LMSpageComponent},
    { path: 'lmsquiz', component: LmsquizComponent},
    { path: 'lmspdf', component: LmspdfComponent},
    { path: 'lmsquizresults', component: LmsquizresultsComponent},
    { path: 'lmsdiscussion', component: LmsdiscussionComponent},
    { path: 'lmsuploadassignment', component: LmsuploadassignmentComponent},
    { path: 'lmsdiscussionpage', component: LmsdiscussionpageComponent},
    { path: 'lmsquizpage', component: LmsquizpageComponent},
    { path: 'lmsassignment', component: LmsassignmentComponent},
    { path: 'profile', component: ProfileComponent},
    { path: 'dashbordbadges', component: DashbordbadgesComponent},
    { path: 'dashbordempty', component: DashbordemptyComponent},
    { path: 'myclass', component: MyclassComponent},
    { path: "**", component: LoginComponent },
];

export const routing: ModuleWithProviders = RouterModule.forRoot(routes);