import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-tabkompetensi',
  templateUrl: './tabkompetensi.component.html',
  styleUrls: ['./tabkompetensi.component.css']
})
export class TabkompetensiComponent implements OnInit {

  constructor() { }
  KompetensiInti:boolean
  KompetensiUmum:boolean
  ngOnInit() {
    this.KompetensiInti=true;
    this.KompetensiUmum=true;
  }
  
  hideKompetensiInti(){
    
      this.KompetensiInti=!this.KompetensiInti;
    
    
  }
  hideKompetensiUmum(){
    this.KompetensiUmum=!this.KompetensiUmum;
  }
}
