import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-lmsdiscussionpage',
  templateUrl: './lmsdiscussionpage.component.html',
  styleUrls: ['./lmsdiscussionpage.component.css','../../assets/css/progressfile.css']
})
export class LmsdiscussionpageComponent implements OnInit {

  constructor() { }

  detilkursus:Boolean
  sesikursus:Boolean
  modalStatus: boolean;
  ngOnInit() {
    this.sesikursus=true;
    this.modalStatus = true
  }
  DetilKursus(){
    this.sesikursus=false;
    this.detilkursus=true;
  }
  SesiKursus(){
    this.sesikursus=true;
    this.detilkursus=false;
  }
  openModal () {
    this.modalStatus = true
  }
  closeModal () {
    this.modalStatus = false
  }
}
