import { Component, OnInit, ElementRef, ViewChild, Input } from '@angular/core';

@Component({
  selector: 'app-videoslider',
  templateUrl: './videoslider.component.html',
  styleUrls: ['./videoslider.component.css','../../assets/css/progressfile.css']
})
export class VideosliderComponent implements OnInit {
  @Input() childMessage: string;
  constructor() { }
  @ViewChild("videoPlayer", { static: false }) videoplayer: ElementRef;
  
  videoControls = {
    Volume: '../../assets/icon/volume.svg',
    Fullscreen: '../../assets/icon/fullscreen.png',
  }

  slideConfig = {"slidesToShow": 1, "slidesToScroll": 1};
  // slideConfig = { "slidesToShow": 1, "slidesToScroll": 1, "accessibility": false, "infinite": true, "fade": true };
  player: any = document.querySelector('.player');
  video: any = document.getElementById('video');
  playBtn: any = document.getElementById('play-btn');
  volumeBtn: any = document.querySelector('.volume-btn');
  volumeSlider: any = document.querySelector('.volume-slider');
  volumeFill: any = document.querySelector('.volume-filled');
  progressSlider: any = document.querySelector('.progress');
  progressFill: any = document.querySelector('.progress-filled');
  textCurrent: any = document.querySelector('.time-current');
  textTotal: any = document.querySelector('.time-total');
  speedBtns: any = document.querySelector('.speed-item');
  fullscreenBtn: any =document.querySelector('.fullscreen'); 
  fullscreen = false;

  //GLOBAL VARS
  lastVolume = 1;
  isMouseDown = false;
  addSlide() {
    // this.slides.push({img: "http://placehold.it/350x150/777777"})
  }
 
 
 
  afterChange(e) {
    console.log('afterChange');
  }
  ngOnInit() {
    // this.modalStatus = true
    var video: any = document.getElementById('video');
    video.pause();	
    video.addEventListener('timeupdate', this.updateProgress);
    video.addEventListener('canplay', this.updateProgress);

  }
  // modalStatus: boolean;
  // closeModal(){
  //   this.modalStatus = false
  // }
  togglePlay() {
    var myVideo: any = document.getElementById("video");
    var playBtn: any = document.getElementById('play-btn');
    var playBigBtn: any = document.getElementById('play-btn-big');
    if (myVideo.paused) {
      myVideo.play();
    } else {
      myVideo.pause();	
    }
    playBtn.classList.toggle('paused');
    playBigBtn.classList.toggle('hide');
  }
  togglePlayBtn() {
    var playBtn: any = document.getElementById('play-btn');
    playBtn.classList.toggle('playing');
  }
  updateProgress(e) {
    var progressFill: any = document.getElementById('progress-filled');
    var myVideo: any = document.getElementById("video");
    var textCurrent: any = document.getElementById('time-current');
    var textDuration: any = document.getElementById('time-total');
    progressFill.style.width = `${myVideo.currentTime/myVideo.duration*100}%`;

    var minutes = Math.floor((myVideo.currentTime % 3600)/60);
    var seconds = Math.floor(myVideo.currentTime % 60)>9?Math.floor(myVideo.currentTime % 60):`0${Math.floor(myVideo.currentTime % 60)}`;
    var currentTime = `${minutes}:${seconds}`;

    var minutess = Math.floor((myVideo.duration % 3600)/60);
    var secondss = Math.floor(myVideo.duration % 60)>9?Math.floor(myVideo.duration % 60):`0${Math.floor(myVideo.duration % 60)}`;
    var duration = `${minutess}:${secondss}`;

    textCurrent.innerHTML = `${currentTime}`;
    textDuration.innerHTML = `${duration}`;
  }
  
  toggleMute() {
    var volumeBtn: any = document.getElementById('volume-btn');
    var volumeFill: any = document.getElementById('volume-filled');
    var myVideo: any = document.getElementById("video");
    if(myVideo.volume) {
      this.lastVolume = myVideo.volume;
      myVideo.volume = 0;
      volumeBtn.classList.add('muted');
      volumeFill.style.width = 0;
    } else {
      myVideo.volume = this.lastVolume;
      volumeBtn.classList.remove('muted');
      volumeFill.style.width = `${this.lastVolume*100}%`;
    }
  }
  changeVolume(e) {
      var volumeSlider: any = document.getElementById('volume-slider');
      var volumeBtn: any = document.getElementById('volume-btn');
      var volumeFill: any = document.getElementById('volume-filled');
      var myVideo: any = document.getElementById("video");
      volumeBtn.classList.remove('muted');
      let volume = e.offsetX/volumeSlider.offsetWidth;
      volume<0.1 ? volume = 0 : volume=volume; 
      volumeFill.style.width = `${volume*100}%`;
      myVideo.volume = volume;
      if (volume > 0.7) {
        volumeBtn.classList.add('loud');
      } else if (volume < 0.7 && volume > 0) {
        volumeBtn.classList.remove('loud');
      } else if (volume == 0) {
        volumeBtn.classList.add('muted');
      }
      this.lastVolume = volume;
  }
  setProgress(e) {
    const newTime = e.offsetX/this.progressSlider.offsetWidth;
    this.progressFill.style.width = `${newTime*100}%`;
    this.video.currentTime = newTime*this.video.duration;
  }
  launchIntoFullscreen(element) {
    if(element.requestFullscreen) {
      element.requestFullscreen();
    } else if(element.mozRequestFullScreen) {
      element.mozRequestFullScreen();
    } else if(element.webkitRequestFullscreen) {
      element.webkitRequestFullscreen();
    } else if(element.msRequestFullscreen) {
      element.msRequestFullscreen();
    }
  }
  exitFullscreen() {
    if(document.exitFullscreen) {
      document.exitFullscreen();
    }
  }
  toggleFullscreen() {
    var player: any = document.getElementById('player');
    this.fullscreen? this.exitFullscreen() : this.launchIntoFullscreen(player)
    this.fullscreen = !this.fullscreen;
  }
}
